<template>
  <div class="log-operationlog">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="log_operationlog_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
        <el-button type="danger" size="mini" @click="exportLog(selectData)">导出</el-button>
      </template>
      <!-- 操作用户名称 -->
      <template v-slot:user_name_head="props">
        <el-input
          v-model="params.user_name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 操作类型 -->
      <template slot="type" slot-scope="props">{{props.data.row.type == 1 ? "用户操作" : "系统管理"}}</template>
      <template v-slot:type_head="props">
        <el-select v-model="params.type" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.type"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 操作IP -->
      <template v-slot:ip="props">
        {{props.data.row.ip}}[{{props.data.row.address}}]
      </template>
      <template v-slot:ip_head="props">
        <el-input v-model="params.ip" :maxLength="20" size="mini" placeholder="请输入内容" @keyup.enter.native="search"></el-input>
      </template>
      <!-- 操作内容 -->
      <template v-slot:content_head="props">
        <el-input
          v-model="params.content"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 状态 -->
      <template slot="status" slot-scope="props">
        <div v-if="props.data.row.status == 1">
          <span v-html="icon.stateIcon.success"></span>成功
        </div>
        <div v-else-if="props.data.row.status == 0">
          <span v-html="icon.stateIcon.error"></span>失败
        </div>
      </template>
      <template v-slot:status_head="props">
        <el-select v-model="params.status" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.status"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 操作时间 -->
      <template v-slot:ctime_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          v-model="params.ctime"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
    </g-el-table>
  </div>
</template>
<script>

export default {
  data() {
    return {
      url: "/history/list",
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "操作用户",
          sortable: true,
          prop: "user_name",
          second_head: {
            width: "150px",
            prop: "user_name",
            templateHeadName: "user_name_head"
          }
        },
        {
          label: "操作类型",
          sortable: true,
          prop: "type",
          second_head: {
            isTemplate: true,
            templateName: "type",
            width: "150px",
            prop: "type",
            templateHeadName: "type_head"
          }
        },
        {
          label: "操作IP",
          prop: "ip",
          sortMethod: this.$tools.IPSort,
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "ip",
            width: "250px",
            templateHeadName: "ip_head",
            prop: "ip"
          }
        },
        {
          label: "操作内容",
          prop: "content",
          sortable: true,
          second_head: {
            minWidth: "200px",
            templateHeadName: "content_head",
            prop: "content"
          }
        },
        {
          label: "状态",
          prop: "status",
          sortable: true,
          second_head: {
            isTemplate: true,
            templateName: "status",
            width: "150px",
            templateHeadName: "status_head",
            prop: "status"
          }
        },
        {
          label: "操作时间",
          prop: "ctime",
          sortable: true,
          second_head: {
            width: "200px",
            templateHeadName: "ctime_head",
            prop: "ctime"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        user_name: "",
        type: "",
        module_id: "",
        ip: "",
        content: "",
        status: "",
        ctime: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData: {
        type: [
          {
            id: 1,
            name: "用户操作"
          },
          {
            id: 2,
            name: "系统管理"
          }
        ],
        module_name: [
          {
            id: 1,
            name: "Netfilter服务"
          }
        ],
        status: [
          {
            id: 1,
            name: "成功"
          },
          {
            id: 0,
            name: "失败"
          }
        ]
      },
      icon: {
        stateIcon: this.$icons.stateIcon
      },
      selectData:[]
    };
  },
  methods: {
    reload() {
      this.$refs.log_operationlog_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select
    },
    exportLog(array) {
      if(this.$tools.isArrayNull(array)){
        this.$message({
          type:"error",
          message:"请选择要导出的日志选项!"
        })
        return
      }
      let str = "序号,操作用户,操作类型,操作IP,操作内容,状态,操作时间,\n"
      var inum = 0
      for(let item of array) {
        inum ++ 
        let type = item.type == 1 ? '用户操作' : "系统管理"
        let status = item.status == 1 ? '成功' : "失败"
        str += `${inum},${item.user_name},${type},${item.ip},${item.content},${status},${item.ctime},\n`
      }
      this.$tools.exportSCV(str,"操作日志")
    }
  }
};
</script>


